import { FC, forwardRef } from 'react';

import { ForwardProps } from '../../types';

import List, { ListProps } from './List';

const OrderedList: FC<ListProps & ForwardProps> = forwardRef(function OrderedList(
  props: ListProps,
  ref: ListProps['ref']
) {
  return <List {...props} variant='ordered' ref={ref} />;
});

export default OrderedList;
